<template><section><h1>CoplanarPolygonGeometry、CoplanarPolygonOutlineGeometry</h1>
<ul>
<li><code v-pre>vc-geometry-polygon-coplanar</code> 组件用于加载共面多边形。</li>
<li><code v-pre>vc-geometry-outline-polygon-coplanar</code> 组件用于加载共面多边形轮廓。</li>
<li>需要作为 <code v-pre>vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre>vc-primitive</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载共面多边形几何体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-polygon-coplanar ref="polygonCoplanar" :positions="positions" :vertexFormat="vertexFormat"></vc-geometry-polygon-coplanar>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributesOutline">
            <vc-geometry-outline-polygon-coplanar ref="polygonCoplanarOutline" :positions="positions"></vc-geometry-outline-polygon-coplanar>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span> <span class="hljs-attr">:attributes</span>=<span class="hljs-string">"attributes"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-polygon-coplanar</span>
            <span class="hljs-attr">ref</span>=<span class="hljs-string">"polygonCoplanar"</span>
            <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions"</span>
            <span class="hljs-attr">:vertexFormat</span>=<span class="hljs-string">"vertexFormat"</span>
          &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-polygon-coplanar</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span> <span class="hljs-attr">:attributes</span>=<span class="hljs-string">"attributesOutline"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-outline-polygon-coplanar</span>
            <span class="hljs-attr">ref</span>=<span class="hljs-string">"polygonCoplanarOutline"</span>
            <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions"</span>
          &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-outline-polygon-coplanar</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">appearance</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">positions</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">110</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">0</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">110</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">200000</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">105</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">200000</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">105</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">0</span> }
        ],
        <span class="hljs-attr">vertexFormat</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">attributes</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">attributesOutline</span>: <span class="hljs-literal">null</span>
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([<span class="hljs-keyword">this</span>.$refs.polygonCoplanar.createPromise, <span class="hljs-keyword">this</span>.$refs.polygonCoplanarOutline.createPromise]).then(
        <span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
          <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'All geometries are loaded.'</span>)
          <span class="hljs-keyword">const</span> boundingSphereUnion = instances.reduce(<span class="hljs-function">(<span class="hljs-params">prev, cur</span>) =&gt;</span> {
            <span class="hljs-keyword">const</span> geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            <span class="hljs-keyword">const</span> boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            <span class="hljs-keyword">return</span> prev === <span class="hljs-literal">null</span> ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, <span class="hljs-literal">null</span>)
          instances[<span class="hljs-number">0</span>].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        }
      )
    },
    <span class="hljs-attr">methods</span>: {
      ready({ Cesium, viewer }) {
        <span class="hljs-keyword">this</span>.viewer = viewer
        <span class="hljs-keyword">const</span> { ColorGeometryInstanceAttribute, PerInstanceColorAppearance } = Cesium
        <span class="hljs-keyword">this</span>.appearance = <span class="hljs-keyword">new</span> PerInstanceColorAppearance({
          <span class="hljs-attr">flat</span>: <span class="hljs-literal">true</span>
        })
        <span class="hljs-keyword">this</span>.vertexFormat = PerInstanceColorAppearance.FLAT_VERTEX_FORMAT
        <span class="hljs-keyword">this</span>.attributes = {
          <span class="hljs-attr">color</span>: <span class="hljs-keyword">new</span> ColorGeometryInstanceAttribute(<span class="hljs-built_in">Math</span>.random(), <span class="hljs-built_in">Math</span>.random(), <span class="hljs-built_in">Math</span>.random(), <span class="hljs-number">0.5</span>)
        }
        <span class="hljs-keyword">this</span>.attributesOutline = {
          <span class="hljs-attr">color</span>: <span class="hljs-keyword">new</span> ColorGeometryInstanceAttribute(<span class="hljs-built_in">Math</span>.random(), <span class="hljs-built_in">Math</span>.random(), <span class="hljs-built_in">Math</span>.random())
        }
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<h3><code v-pre>vc-geometry-polygon-coplanar</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Object|Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 polygon 的 positions 属性。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 polygon 要缓存的顶点属性。</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number|Object</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定 polygon 纹理按正北方向逆时针旋转角度。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 polygon 参考椭球体。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre>vc-geometry--outline-polygon-coplanar</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Object|Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 polygon 的 positions 属性。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>positions 结构</li>
</ul>
<pre v-pre><code v-pre class="language-js"><span class="hljs-comment">// Array</span>
[{<span class="hljs-attr">lng</span>: number, <span class="hljs-attr">lat</span>: number, <span class="hljs-attr">height</span>: number},...,{<span class="hljs-attr">lng</span>: number, <span class="hljs-attr">lat</span>: number, <span class="hljs-attr">height</span>: number}]
</code></pre>
<p>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PolygonGeometry.html">CoplanarPolygonGeometry</a></strong>、<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CoplanarPolygonOutlineGeometry.html">CoplanarPolygonOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          positions: [
            { lng: 110, lat: 33.5, height: 0 },
            { lng: 110, lat: 33.5, height: 200000 },
            { lng: 105, lat: 33.5, height: 200000 },
            { lng: 105, lat: 33.5, height: 0 }
          ],
          vertexFormat: null,
          attributes: null,
          attributesOutline: null
        }
      },
      mounted() {
        Promise.all([this.$refs.polygonCoplanar.createPromise, this.$refs.polygonCoplanarOutline.createPromise]).then(
          (instances) => {
            console.log('All geometries are loaded.')
            const boundingSphereUnion = instances.reduce((prev, cur) => {
              const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
              const boundingSphere = cur.vm.$parent.modelMatrix
                ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
                : geometry.boundingSphere
              return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
            }, null)
            instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
          }
        )
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { ColorGeometryInstanceAttribute, PerInstanceColorAppearance } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            flat: true
          })
          this.vertexFormat = PerInstanceColorAppearance.FLAT_VERTEX_FORMAT
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
          this.attributesOutline = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random())
          }
        }
      }
    }
  </script>